export type SvgIconDataName = keyof typeof SvgIconData;
export const SvgIconNames = Object.fromEntries(Object.keys(SvgIconData).map((key) => [key, key])) as {
  [K in SvgIconDataName]: K;
};

export const LJ_SVG_ICON_CLASS = 'lj_svg_icon';
export const LJ_SVG_ICON_STYLE_ID = 'lj_svg_icon_style';
export const loadSvgIconStyles = () => {
  if (typeof document === 'undefined' || document.getElementById(LJ_SVG_ICON_STYLE_ID)) return;
  let iconStyles = '';
  for (const key of Object.keys(SvgIconData)) {
    iconStyles += `.${LJ_SVG_ICON_CLASS}_${key} { mask-image: url("${SvgIconData[key]}"); }\n`;
  }

  const style = document.createElement('style');
  style.id = LJ_SVG_ICON_STYLE_ID;
  style.innerHTML = `
      .${LJ_SVG_ICON_CLASS} {
        display: inline-block;
        width: 24px;
        height: 24px;
        vertical-align: middle;
        mask-repeat: no-repeat;
        mask-position: center;
        background-color: var(--primary-color, #ccc);
        overflow:hidden;
        mask-size: contain;
      }
      .${LJ_SVG_ICON_CLASS}:hover, .${LJ_SVG_ICON_CLASS}.active {
        background-color: var(--primary-accent-color, #0a74c9);
      }
      ${iconStyles}
    `;
  document.head.appendChild(style);
};
